import { useLocation, useSearchParams } from "react-router-dom";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import { Suspense } from "react";
import QvipleLoader from "../../Loader/QvipleLoader";
import UserMemberTabsWrapper from "../../Ui/UserMember/UserMemberTabsWrapper";
import Loading from "../../Loader/Loading";
import StoreManagerProfile from "./StoreManagerProfile";
import StoreUnitConfig from "./StoreUnit/StoreUnitConfig";
import { useStoreDashboardMaster } from "../../Hooks/ApiHooks/StoreManager/store-manager-api";
import StoreIndividualStaff from "./StoreIndividualStaff/StoreIndividualStaff";
import StoreSevenDepartmentConfig from "./StoreSeven/Config/StoreSevenDepartmentConfig";
import StoreStockConfig from "./StoreStock/StoreStockConfig";
import StoreSevenUnitCustomConfig from "./StoreSeven/Config/StoreSevenUnitCustomConfig";
import StoreSevenStaffIndividualConfig from "./StoreSeven/Config/StoreSevenStaffIndividualConfig";
import StoreSevenDepartmentClassConfig from "./StoreSeven/Config/StoreSevenDepartmentClassConfig";
import StoreSevenStockCategoryConfig from "./StoreSeven/Config/StoreSevenStockCategoryConfig";
import StoreIssueConfig from "./StoreIssue/StoreIssueConfig";

const StoreManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { storeDashboardMaster, storeDashboardMasterLoading } =
    useStoreDashboardMaster({
      data: {
        stid: getQuery.state?.stid,
        mid: "",
      },
      skip: !getQuery.state?.stid,
    });

  //   console.info("storeDashboardMaster", storeDashboardMaster);
  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ||
          ["day-book", "masters"]?.includes(searchParams.get("type")) ? (
            <UserMemberTabsWrapper>
              <StoreManagerProfile
                tabManage={storeDashboardMaster?.tab_manage}
              />
              {storeDashboardMasterLoading && <Loading />}
            </UserMemberTabsWrapper>
          ) : searchParams.get("type") === "unit" ? (
            <StoreUnitConfig
              instituteId={storeDashboardMaster?.institute?._id}
              fid="651ba371e39dbdf817dd5285"
              //   fid={storeDashboardMaster?.institute?.financeDepart?.[0]}
            />
          ) : searchParams.get("type") === "individual-staff" ? (
            <StoreIndividualStaff
              instituteId={storeDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "stock" ? (
            <StoreStockConfig
              instituteId={storeDashboardMaster?.institute?._id}
              stid={getQuery.state?.stid}
            />
          ) : searchParams.get("type") === "store-department" ? (
            <StoreSevenDepartmentConfig
              instituteId={storeDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "store-unit-custom" ? (
            <StoreSevenUnitCustomConfig
              instituteId={storeDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "store-individual" ? (
            <StoreSevenStaffIndividualConfig
              instituteId={storeDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "store-cls" ? (
            <StoreSevenDepartmentClassConfig
              instituteId={storeDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "store-stock" ? (
            <StoreSevenStockCategoryConfig
              instituteId={storeDashboardMaster?.institute?._id}
            />
          ) : searchParams.get("type") === "issue" ? (
            <StoreIssueConfig
              instituteId={storeDashboardMaster?.institute?._id}
              stid={getQuery.state?.stid}
            />
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default StoreManager;
